.nav-pills__unread-indicator{display:flex;justify-content:center;align-items:center;position:absolute;margin-left:50%;padding:8px;width:188px;height:auto;font-size:14px;line-height:16px;text-align:center;border-radius:4px;box-shadow:0px 4px 6px rgba(0,0,0,0.12);font-weight:700;z-index:2;cursor:pointer;transform:translateX(-50%);opacity:0;visibility:hidden;background:var(--mention-bg);color:var(--mention-color)}.nav-pills__unread-indicator .icon{display:inline-flex;justify-content:center;align-items:center;margin-right:8px}body.enable-animations .nav-pills__unread-indicator{transition-property:opacity, visibility;transition-duration:.12s;transition-delay:0s;transition-timing-function:ease-out, step-start}.nav-pills__unread-indicator svg{fill:var(--mention-color)}.nav-pills__unread-indicator-top{top:16px}.nav-pills__unread-indicator-top .icon svg{transform:rotate(180deg)}.nav-pills__unread-indicator-bottom{bottom:16px}.nav-pills__unread-indicator--visible{opacity:1;visibility:visible}body.enable-animations .nav-pills__unread-indicator--visible{transition-property:opacity, visibility;transition-duration:.12s;transition-delay:0s;transition-timing-function:ease-out, step-start}@media screen and (max-width: 768px){.nav-pills__unread-indicator{width:260px}}@media screen and (max-width: 480px){.nav-pills__unread-indicator{width:230px}}@media screen and (max-width: 320px){.multi-teams .nav-pills__unread-indicator{width:190px}}
