.about-modal.cloud{position:absolute;margin:auto;top:50%;left:50%;transform:translate(-50%, -50%) !important}.about-modal.cloud .about-modal__hash{padding-bottom:20px}.about-modal.cloud .modal-content{overflow:hidden;border:1px solid rgba(var(--center-channel-color-rgb), 0.16);box-shadow:0px 20px 32px rgba(0,0,0,0.12);border-radius:8px}.about-modal.cloud .modal-content .modal-header{width:600px;height:66px}.about-modal.cloud .modal-content .modal-body{overflow:hidden;padding:0px 50px;width:600px}.about-modal.cloud .modal-content .modal-body .about-modal__logo{padding-left:0;padding-right:32px}.about-modal.cloud .modal-content .modal-body .about-modal__logo svg{height:104px;width:104px}.about-modal.cloud .modal-content .modal-body .about-modal__title{margin:0;font-weight:600;font-size:24px;line-height:32px}.about-modal.cloud .modal-content .modal-body .about-modal__content{padding-top:46px}.about-modal.cloud .modal-content .modal-body .subtitle{width:294px;height:24px;left:0px;top:32px;font-size:16px;line-height:24px;color:var(--center-channel-color);margin-bottom:16px}.about-modal.cloud .modal-content .modal-body .description,.about-modal.cloud .modal-content .modal-body .form-group{width:342px;height:20px;left:0px;top:0px;font-size:14px;line-height:20px;color:rgba(var(--center-channel-color-rgb), 0.64);align-self:center;margin:0px 0px}.about-modal.cloud .modal-content .modal-body .about-footer{margin-top:16px;font-size:12px;line-height:16px;color:rgba(var(--center-channel-color-rgb), 0.64)}.about-modal.cloud .modal-content .modal-body .about-footer .copy-right{margin-top:12px}
