.Card{border:1px solid rgba(var(--center-channel-color-rgb), 0.08);box-shadow:0px 2px 3px rgba(0,0,0,0.08);border-radius:4px;background-color:var(--center-channel-bg);transition:box-shadow 0.3s ease-in-out}.Card+.Card{margin-top:16px}.Card.expanded{box-shadow:0px 8px 24px rgba(0,0,0,0.12)}.Card:not(.expanded):hover{border-color:rgba(var(--center-channel-color-rgb), 0.16);box-shadow:0px 4px 6px rgba(0,0,0,0.12);transition-duration:0.15s}.Card__header hr.Card__hr{margin:0 12px;border-color:rgba(var(--center-channel-color-rgb), 0.08)}.Card__body{overflow:hidden;height:0;margin:0 12px}.Card__body.expanding{transition-duration:0.3s;transition-timing-function:ease-in-out;transition-property:height, margin}.Card__body.expanded{margin:12px}.Card__body.expanded:not(.expanding){height:auto}@media screen and (max-width: 768px){.Card+.Card{margin-top:12px}}
