.TeamProfileStep{display:flex;padding:24px 40px}.TeamProfileStep h3{font-weight:600;font-size:14px;line-height:20px;color:var(--center-channel-color);margin:0}.TeamProfileStep__profilePicture{flex-grow:0}.TeamProfileStep__profilePicture .PictureSelector{margin-top:12px}.TeamProfileStep__profilePicture .PictureSelector .PictureSelector__image{border-radius:8px}.TeamProfileStep__profilePicture .PictureSelector .PictureSelector__selectButton{top:-6px;right:-8px}.TeamProfileStep__profilePicture .TeamIcon__initials{font-size:38px;line-height:21px;padding:35px 0;text-transform:capitalize;text-align:center;font-weight:600;color:var(--sidebar-text);background-color:var(--sidebar-bg);border-radius:8px}.TeamProfileStep__textInputs{margin-left:64px;flex-grow:0.75;max-width:600px}.TeamProfileStep__textInputs .Input_wrapper{font-size:14px;line-height:38px;padding:0 16px}.TeamProfileStep__textInputs .Input{font-size:14px;line-height:38px}.TeamProfileStep__textInputs .Input_legend{margin-left:12px}.TeamProfileStep__textInputs .Input_container{margin-top:28px}.TeamProfileStep__pictureError{font-size:12px;line-height:16px;color:var(--error-text);padding:0 40px}.TeamProfileStep__pictureError>span{margin-left:4px}.TeamProfileStep__pictureError i{font-size:14px;vertical-align:middle;margin-top:-2px}@media screen and (max-width: 1020px){.TeamProfileStep{flex-direction:column;padding:4px 12px 24px 12px}.TeamProfileStep__textInputs{margin-left:0;margin-top:32px;max-width:unset}.TeamProfileStep__textInputs .Input_container{margin-top:20px}}
