.Avatar{border-radius:50%;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;vertical-align:sub}.Avatar.Avatar-xxs{width:16px;min-width:16px;height:16px;font-size:8px}.Avatar.Avatar-xs{width:20px;min-width:20px;height:20px;font-size:9.5px}.Avatar.Avatar-sm{width:24px;min-width:24px;height:24px;font-size:10px}.Avatar.Avatar-md{width:32px;min-width:32px;height:32px;font-size:12px}.Avatar.Avatar-lg{width:36px;min-width:36px;height:36px;font-size:14px}.Avatar.Avatar-xl{width:50px;min-width:50px;height:50px;font-size:18px}.Avatar.Avatar-xxl{width:128px;min-width:128px;height:128px;font-size:44px}.Avatar.Avatar-plain{display:inline-flex;position:relative;align-items:center;justify-content:center;background:white}.Avatar.Avatar-plain::before{content:attr(data-content);display:inline-flex;align-items:center;justify-content:center;position:absolute;width:100%;height:100%;border-radius:50%;background:rgba(var(--center-channel-color-rgb), 0.08);color:rgba(var(--center-channel-color-rgb), 0.64)}
