.SidebarNextSteps{width:100%;color:var(--sidebar-text);height:82px;padding:8px 16px 18px 14px;align-self:flex-end;position:relative;background-color:var(--sidebar-bg);transition:0.2s ease-in-out;cursor:pointer}.SidebarNextSteps.tips{height:70px}.SidebarNextSteps:hover:not(.active){background-color:rgba(var(--button-color-rgb), 0.08)}.SidebarNextSteps.active{background-color:var(--sidebar-text-hover-bg)}.SidebarNextSteps.active::before{content:'';height:100%;position:absolute;left:-2px;top:0;width:4px;background:var(--sidebar-text-active-border);border-radius:4px}.SidebarNextSteps__top{display:flex}.SidebarNextSteps__top span{font-weight:600;font-size:14px;line-height:28px}.SidebarNextSteps__close{border:none;border-radius:4px;background:none;padding:0;margin-left:auto;font-size:14.4px;line-height:14px;align-self:center;width:28px;height:28px}.SidebarNextSteps__close i{color:var(--sidebar-text);opacity:0.72}.SidebarNextSteps__close i::before{margin:0}.SidebarNextSteps__close:hover{background-color:var(--sidebar-text-hover-bg)}.SidebarNextSteps__close:hover i{opacity:1}.SidebarNextSteps__middle{font-size:12px;line-height:16px}.SidebarNextSteps__progressBar{margin-top:8px;height:4px;border-radius:4px;background-color:rgba(var(--sidebar-text-rgb), 0.16)}.SidebarNextSteps__progressBar .ProgressBar{border-radius:4px;overflow:hidden}.SidebarNextSteps__progressBar .ProgressBar__progress{border-radius:4px;background-color:var(--sidebar-text)}
