.CompleteProfileStep{display:flex;padding:24px 40px}.CompleteProfileStep h3{font-weight:600;font-size:14px;line-height:20px;color:var(--center-channel-color);margin:0}.CompleteProfileStep__profilePicture{flex-grow:0}.CompleteProfileStep__profilePicture .PictureSelector{margin-top:12px}.CompleteProfileStep__fullName{margin-left:64px;flex-grow:0.75;max-width:600px}.CompleteProfileStep__fullName .Input_wrapper{font-size:14px;line-height:38px;padding:0 16px}.CompleteProfileStep__fullName .Input{font-size:14px;line-height:38px}.CompleteProfileStep__fullName .Input_legend{margin-left:12px}.CompleteProfileStep__fullName .Input_container{margin-top:28px}.CompleteProfileStep__pictureError{font-size:12px;line-height:16px;color:var(--error-text);padding:0 40px}.CompleteProfileStep__pictureError>span{margin-left:4px}.CompleteProfileStep__pictureError i{font-size:14px;vertical-align:middle;margin-top:-2px}@media screen and (max-width: 1020px){.CompleteProfileStep{flex-direction:column;padding:4px 12px 24px 12px}.CompleteProfileStep__fullName{margin-left:0;margin-top:32px;max-width:unset}.CompleteProfileStep__fullName .Input_container{margin-top:20px}}
